import React from "react"
import { Field, reduxForm } from "redux-form"
import { Location } from "@reach/router"
import posed from "react-pose"
import styles from "./messageForm.module.sass"

const ThankYouMessage = posed.div({
  enter: { opacity: 1 },
  exit: { opacity: 0 },
})

class MessageForm extends React.Component {
  componentDidMount() {
    this.props.setPage(this.props.location)
  }

  render() {
    const { handleSubmit, hasSubmittedMessageForm } = this.props
    return (
      <div className={styles.contactFormWrapper}>
        <form onSubmit={handleSubmit}>
          <div className={styles.inner}>
            {hasSubmittedMessageForm ? (
              <ThankYouMessage
                pose={hasSubmittedMessageForm ? "enter" : "exit"}
                initialPose="exit"
              >
                <h4>Thanks, we will get back to you soon!</h4>
              </ThankYouMessage>
            ) : (
              <>
                <div className={styles.twoColumns}>
                  <div>
                    <label htmlFor="firstName">First Name</label>
                    <Field
                      name="firstName"
                      component="input"
                      type="text"
                      placeholder="Jonathan"
                    />
                  </div>
                  <div>
                    <label htmlFor="lastName">Last Name</label>
                    <Field
                      name="lastName"
                      component="input"
                      type="text"
                      placeholder="Snow"
                    />
                  </div>
                </div>
                <div>
                  <label htmlFor="email">Email</label>
                  <Field
                    name="email"
                    component="input"
                    type="email"
                    placeholder="jonathan@thewall.com"
                  />
                </div>
                <div>
                  <label htmlFor="messageType">Message Type</label>
                  <Field
                    name="messageType"
                    component="select"
                    type="select"
                    placeholder="Select a message type"
                  >
                    <option>Enterprise</option>
                    <option>Hosted</option>
                    <option>General Questions</option>
                    <option>Partnership Opportunity</option>
                  </Field>
                </div>
                <div>
                  <label htmlFor="email">Message</label>
                  <Field
                    name="message"
                    component="textarea"
                    type="text"
                    placeholder="Tell us about your project..."
                  />
                </div>
                <div className={styles.contactCTASection}>
                  <button type="submit" className={styles.sendMessage}>
                    <span>Send Message</span>
                  </button>
                </div>
                <div className={styles.contactCTASection}>
                  <h5 className={styles.bold}>
                    Make sure the email is correct so we can reach you.
                  </h5>
                  <h5>Usually we respond within 24 hours.</h5>
                </div>
              </>
            )}
          </div>
        </form>
      </div>
    )
  }
}

MessageForm = reduxForm({
  form: "messageUs",
})(MessageForm)

export default props => (
  <Location>
    {locationProps => <MessageForm {...locationProps} {...props} />}
  </Location>
)
