import { connect } from "react-redux"
import MessageForm from "../components/messageForm/MessageForm"
import { sendMessage, setPage } from "../actions/forms"

function mapStateToProps(state) {
  const { hasSubmittedMessageForm } = state.pageReducer
  return {
    hasSubmittedMessageForm,
  }
}

function mapDispatchToProps(dispatch) {
  return {
    onSubmit: data => dispatch(sendMessage(data)),
    setPage: location => dispatch(setPage(location)),
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MessageForm)
